import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, ViewController } from 'ionic-angular';
import { Note, Collections } from '../../app/app.models';
import { AuthProvider } from '../../providers/auth/auth';
import { Observable } from 'rxjs';
import { AngularFirestore } from 'angularfire2/firestore';
import { DataProvider } from '../../providers/data/data';
import { groupBy } from 'lodash';

@IonicPage()
@Component({
  selector: 'page-notas',
  templateUrl: 'notas.html',
})
export class NotasPage {

  public id: string = this.navParams.get('id')
  public notas: Observable<Note[]>;

  public sortNotas;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private modal: ModalController,
    public viewCtrl: ViewController,
    private auth: AuthProvider,
    private afs: AngularFirestore,
    private data: DataProvider,
  ) {
  }

  ionViewDidLoad() {

    this.auth.user$.subscribe(user => {
      if (user && !this.notas) this.loadNotas(user.uid, true);
    })

    if (this.id) this.loadNotas(this.id, false);

    /* this.back.getNotas(this.id, this.type).subscribe(data => {
      this.notas = data
    }, err => {
      this.toast.create({message: 'No se pudieron cargar tus notas...', duration: 2000}).present()
      if (this.navCtrl.canGoBack()) this.navCtrl.pop()
    }) */

  }

  async loadNotas(id: string, user: boolean) {
    if (user) {

      const notas = await this.data.getCollectionQuery<Note>(Collections.NOTE, ref => ref
        .where('user', '==', id));

      this.sortNotas = Object.values(groupBy(notas.filter(n => n.parent_type), 'parent_type'));

    } else {
      this.notas = this.afs.collection<Note>(Collections.NOTE, ref => ref.where('parent_id', '==', id)).valueChanges();
    }
  }

  openAdd(id: string, type: string) {

    const m = this.modal.create('NotasAddPage', {id, type})
    m.present()

    m.onDidDismiss(data => {
      if(!data) return
      this.ionViewDidLoad()
    })

  }

}



